@import "../App.scss";

@media (min-width: 601px) {
  .Mountains {
    width: 100%;
    height: 30vh;
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 70vh;
    z-index: 2;
  }

  .Block {
    position: relative;
    display: flex;
    justify-content: center;

    &.Sky {
      position: static;
      width: 100%;
      height: 100vh;
      top: 0;
      z-index: -1;
    }

    &.Work {
      color: white;
      background: $dark-blue;
    }

    &.School {
      background: $light-yellow;
      color: rgb(4, 25, 56);
      padding-bottom: 4vh;
    }

    &.Connect {
      background: $light-blue;
      color: $dark-blue;
    }
  }

  .InnerBlock {
    width: 80%;
    margin: 5vh;

    &.Intro {
      display: inline-block;
      position: fixed;
      margin-top: 20vh;
      height: 60vh;
      z-index: 0;
    }

    &.Resume {
      width: 90%;
    }
  }

  .IntroLeft {
    width: 70%;
  }

  .IntroRight {
    width: 30%;
  }

  .Header {
    font-family: Pixelated;
    text-align: left;

    &.Title {
      padding-top: 2vw;
      padding-bottom: 4vw;
    }

    &.L {
      font-size: 8vw;
    }

    &.ML {
      font-size: 3vw;
    }

    &.M {
      font-size: 2.5vw;
    }

    &.S {
      font-size: 2vw;
    }

    &.XS {
      font-size: 1.5vw;
    }

    &.White {
      color: white;
    }

    &.Blue {
      color: $dark-blue;
    }

    &.LightBlue {
      color: $light-blue;
    }

    &.Yellow {
      color: $light-yellow;
    }

    &.Gold {
      color: $dark-yellow;
    }

    &.Right {
      text-align: right;
    }

    &.Center {
      text-align: center;
    }

    &.Top {
      position: absolute;
      top: 2.5vw;
      transform: rotate(-90deg);
    }

    &.Bottom {
      position: absolute;
      bottom: 2.5vw;
      transform: rotate(-90deg);
    }
  }

  .DatesBlock {
    position: relative;
    height: 100%;
  }

  .VerticalBox {
    flex: 1;
    position: relative;
    padding-right: 20;
  }

  .Text {
    flex: 1;
    font-family: OpenSans;
    font-size: 1.5vw;
    text-align: left;
  }

  .Links {
    text-align: right;

    li {
      list-style-type: none;
    }

    a {
      display: inline-block;
      line-height: 2vh;
      vertical-align: middle;
      text-decoration: none;
    }
  }

  .LinkLine {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .UvaLogo {
    height: 30vh;
    width: 30vh;
    padding-left: 3vh;
    background-size: cover;
    background-position: center;
  }

  .FlexRow {
    display: flex;
    flex-direction: row;

    &.Logos {
      padding-top: 3vw;
      padding-bottom: 4vw;
      padding-left: 1vw;
      padding-right: 1vw;
    }
  }

  .FlexBox {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .ClickBox {
    background-size: cover;
    background-position: center;
    
    &.Github {
      width: 17.65vw;
      height: 17.25vw;
      margin-top: 0.5vw;
      border: solid rgb(23,23,21);
      border-radius: 13vw;
    }
    &.LinkedIn {
        width: 17vw;
        height: 17vw;
        margin-top: 0.5vw;
        border: solid rgb(47,116,174);
        border-radius: 2vw;
      }
      &.Gmail {
        width: 18.75vw;
        height: 16.5vw;
        margin-top: 0.5vw;
        border-radius: 2.2vw;
      }
  }

  .ClickBox:hover {
    background-color: white;
  }
}

